var DEVICE_LIST_TPL = require('./device-list.component.tpl.html');
function DeviceListComponent() {
    return {
        template: DEVICE_LIST_TPL,
        scope: {},
        bindToController: {},
        controllerAs: 'deviceList',
        controller: DeviceListController
    };
}
exports.DeviceListComponent = DeviceListComponent;
var DeviceListController = (function () {
    /* @ngInject */
    function DeviceListController(deviceList) {
        var _this = this;
        this.deviceList = deviceList;
        this.loading = false;
        this.hasSelected = false;
        this.isAllSelected = false;
        this.selectedCount = 0;
        this.load = function () {
            _this.loading = true;
            _this.deviceList.loadDevices(_this.pageNumber, _this.pageSize).finally(function () {
                _this.loading = false;
                _this.synchronizePage();
            });
        };
        this.activate = function () {
            _this.loading = true;
            _this.deviceList.activateDevices().finally(function () {
                _this.loading = false;
                _this.synchronizePage();
            });
        };
        this.deactivate = function () {
            _this.loading = true;
            _this.deviceList.deactivateDevices().finally(function () {
                _this.loading = false;
                _this.synchronizePage();
            });
        };
        this.delete = function () {
            _this.loading = true;
            _this.deviceList.deleteDevices().finally(function () {
                _this.loading = false;
                _this.synchronizeSelection();
                _this.synchronizePage();
            });
        };
        this.export = function () {
            _this.deviceList.saveToCSV();
        };
        this.selectAll = function (selected) {
            _this.deviceList.selectAll(selected);
            _this.synchronizeSelection();
        };
        this.select = function (item, selected) {
            _this.deviceList.select(item, selected);
            _this.synchronizeSelection();
        };
        this.pageSize = deviceList.size;
        this.synchronizePage();
    }
    Object.defineProperty(DeviceListController.prototype, "hasNoData", {
        get: function () {
            return this.items.length === 0;
        },
        enumerable: true,
        configurable: true
    });
    DeviceListController.prototype.changePage = function () {
        this.isAllSelected = false;
        this.load();
    };
    DeviceListController.prototype.changeSize = function () {
        this.isAllSelected = false;
        this.pageNumber = 1;
        this.load();
    };
    DeviceListController.prototype.synchronizePage = function () {
        this.items = this.deviceList.data;
        this.pageNumber = this.deviceList.page;
        this.totalCount = this.deviceList.totalCount;
    };
    DeviceListController.prototype.synchronizeSelection = function () {
        this.hasSelected = this.deviceList.hasSelected;
        this.selectedCount = this.deviceList.selectedCount;
    };
    return DeviceListController;
})();



